#element-modal {
    position: absolute;
    top: 10%;
    align-self: center;
    left: calc(50vw - 400px);
    z-index: 2;
    width: 800px;
    border-radius: 30px;
    background-color: white;
    font-family: 'Sen', sans-serif;
    color: rgb(63, 163, 245);
}

.grid-container {
    display: grid;
    width: 96%;
    padding: 2%;
    grid-template-columns: 1fr 50px;
    grid-gap: 5px;
}

.item1 {
    grid-column: 1;
    font-size: 40px;
    padding-left: 30px;
}

.item2 {
    grid-column: 2;
    background-color: transparent;
    font-size: 27px;
    width: 50px;
    color: white;
    border: 3px solid rgb(41, 142, 224);
    background-color: rgb(63, 163, 245);
    border-radius: 50%;
}

.item2:hover {
    cursor: pointer;
}

.item3 {
    display: none;
}

.item4 {
    font-size: 20px;
    grid-column: 1 / span 2;
    text-align: center;
}

@media only screen and (min-width: 740px) {
    #element-modal {
        background-color: transparent;
    }
    .modal_content {
        width: 720px;
        border-radius: 30px;
        background-color: rgba(243, 243, 243, 0.671);
        -webkit-backdrop-filter: blur(10px);
                backdrop-filter: blur(10px);
        color: black;
        border: 5px solid white;
        font-family: 'Sen', sans-serif;
        margin: auto;
        box-shadow: 5px 10px 10px 0px rgba(0, 0, 0, 0.507);
    }
    .grid-container {
        display: grid;
        width: 710px;
        grid-template-columns: 250px 1fr 100px;
        grid-gap: 5px;
        margin: 5px;
        margin-bottom: 20px;
        padding: 0px;
    }
    .item1 {
        grid-column: 1 / span 2;
        font-size: 40px;
        /* font-family: 'Sen', sans-serif; */
        color: black;
        padding-left: 50px;
    }
    .item2 {
        grid-column: 3;
        color: white;
        background-color: transparent;
        border: none;
        font-size: 27px;
        border-radius: 25px;
        outline: none;
        margin-left: 40%;
        width: 50%;
    }
    .item2:hover {
        transition-duration: 0.3s;
        background-color: rgb(10, 89, 194);
    }
    .item3 {
        display: block;
        grid-row: 2 / span 2;
        padding-left: 20px;
        width: 230px;
    }
    .item4 {
        grid-column: 2;
        grid-row: 2;
        text-align: left;
        font-size: 17px;
        padding-left: 10px;
    }
    .item5 {
        display: block;
        grid-column: 3;
        grid-row: 2;
        width: 80px;
        height: auto;
        border-radius: 50px;
        margin-left: 10px;
    }
    .item6 {
        grid-row: 3;
        grid-column: 3;
        opacity: 0;
    }
    .item7 {
        grid-column: 2 / span 2;
    }
    .mobile_radioactive_indictaion {
        display: none;
    }
}
.slider_div {
    display: none;
    max-width: 700px;
    width: 90%;
    margin: auto;
    margin-top: -5px;
}

.slider {
    -webkit-appearance: none;
    appearance: none;
    outline: none;
    width: 100%;
    border-radius: 15px;
    height: 20px;
    background-color: rgba(255, 255, 255, 0.466);
    margin-top: 10px;
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: white;
    cursor: pointer;
}

.slider::-moz-range-thumb {
    width: 40px;
    height: 40px;
    border: none;
    background: white;
    cursor: pointer;
    border-radius: 50%;
}

.slider_div p {
    background-color: transparent;
    font-family: 'Sen', sans-serif;
    color: white;
    text-align: center;
    margin: 0px;
    font-size: 1.5em;
    margin-top: 5px;
    line-height: 21px;
    margin-bottom: -5px;
}

@media only screen and (min-width: 1000px) {
    .slider {
        border-radius: 15px;
        height: 15px;
        margin-top: 5px;
    }
    .slider::-webkit-slider-thumb {
        width: 30px;
        height: 30px;
    }
    .slider::-moz-range-thumb {
        width: 30px;
        height: 30px;
    }
}
#slider-menu {
    width: 90%;
    max-width: 700px;
    display: grid;
    margin: auto;
    margin-top: 70px;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 5px;
    -webkit-column-gap: 5px;
            column-gap: 5px;
}

#slider-menu button {
    background-color: transparent;
    color: white;
    border: 2px solid white;
    border-radius: 20px;
    font-size: 1.1em;
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: 'Sen', sans-serif;
    font-weight: 500;
}

#slider-menu button:hover {
    background-color: white;
    color: black;
    cursor: pointer;
}

#slider-menu button:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px);
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

